<template>
  <div>
    <swiper :options="swiperOption" class="swi">
      <swiper-slide v-for="item in bookList" :key="item.id" class="img" @click.native="bookHandle(item.id)" >
          <img :src="'https://images.weserv.nl/?url='+item.cover.url" alt="">
          <div>{{item.title}}</div>
          <div>{{item.author[0]}}</div>

      </swiper-slide>
      
      <div class="swiper-pagination" slot="pagination"></div>
    </swiper>
  </div>
</template>

<script>
import 'swiper/dist/css/swiper.css'
import { swiper, swiperSlide } from "vue-awesome-swiper";
export default {
  data() {
    return {
      swiperOption: {
        slidesPerView: 3,
        spaceBetween: 30,
        pagination: {
          el: ".swiper-pagination",
          clickable: true
        }
      },
      bookList:[]

    };
  },
  components: {
    swiper,
    swiperSlide
  },
  created() {
      this.getData()
  },
  methods: {
      getData(){
        let birdapi = "https://bird.ioliu.cn/v2?url=";
        let bookApi = `https://m.douban.com/rexxar/api/v2/subject_collection/book_fiction/items?start=0&count=10`
        axios.get(birdapi + bookApi).then((res) =>{
        // console.log(res);
        this.bookList = res.data.subject_collection_items
        console.log(this.bookList);
        })
      },
      bookHandle(id){
        this.$router.push({
            name:'bookdetail',
            params:{
                id
            }
        })          
      }
  },
};
</script>
<style scoped>

.img img{
    display: inline-block;
    width: 100px;
    height: 100px;
    
}
.img{
    font-size: .3rem;
    text-align: center;
}
.swi{
    margin-top: 10px;
}
</style>
